import React from 'react';
import styled from 'styled-components';
import dayjs from 'dayjs';


const SummaryWrapper = styled.div`
  background: #ffffff;
  margin: 10px 8px;
  padding: 10px 4px;
  > div{
    padding: 4px 0;
    color: #333;
  }
  > .last{
    display:flex;
    justify-content: space-between;
  }
`;

type Props = {
  incomeNum:number,
  expenseNum:number,
  incomeSum:number,
  expenseSum:number
}
const Summary:React.FC<Props> = (props:Props) => {
  return (
    <SummaryWrapper>
      <div>{dayjs().format('YYYYMM')}</div>
      <div>{props.expenseNum}   ${props.expenseSum}</div>
      <div className='last'>
        <div>{props.incomeNum}  ${props.incomeSum}</div>
        <div> ${props.incomeSum - props.expenseSum}</div>
      </div>
    </SummaryWrapper>
  );
};

export {Summary};