import React from 'react';



type Props = {
  type: ('-' | '+')
  onChange: (type: ('-' | '+')) => void
}
const ChooseType: React.FC<Props> = (props: Props) => {
  return (
    <>
      <li className={props.type === '-' ? 'select' : ''}
          onClick={() => props.onChange('-')}
      >
      </li>
      <li className={props.type === '+' ? 'select' : ''}
          onClick={() => props.onChange('+')}
      >
      </li>
    </>
  );
};


export {ChooseType};